.form-field {
  margin: 30px 0;
}

label {
  color: #7950f2;
  display: block;
  margin: 7px 15px;
}

.inputs {
  padding: 10px;
  margin: 0 12px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 300px;
}

.inputs:focus {
  outline: none;
  border: 1.5px solid #1a73e8;
}
input[type="radio"],
input[type="checkbox"] {
  margin: 15px;
}

.secondstep,
.firststep {
  animation: helo 0.5s ease-out;
}

@keyframes helo {
  0% {
    transform: translateX(200px);
    opacity: 0;
  }
  100% {
    transform: translate(0);
    opacity: 1;
  }
}
